import React, { useState } from 'react';
import { FaTachometerAlt, FaShoppingCart, FaTags, FaUsers, FaChartLine, FaCog } from 'react-icons/fa';
import styles from '../styles/Sidebar.module.css';

const Sidebar = ({ toggleSidebar }) => {
  const [isCollapsed, setIsCollapsed] = useState(false);

  const handleToggleSidebar = () => {
    setIsCollapsed(!isCollapsed);
    toggleSidebar(!isCollapsed);
  };

  return (
    <div className={`${styles.sidebar} ${isCollapsed ? styles.collapsed : ''}`}>
      <button className={styles.toggleButton} onClick={handleToggleSidebar}>
        {isCollapsed ? '→' : '←'}
      </button>
      <div className={styles.logo}>
        <img src="/logo.png" alt="Logo" />
        {!isCollapsed && <span>Fashion Admin</span>}
      </div>
      <ul className={styles.navList}>
        <li><a href="/dashboard"><FaTachometerAlt />{!isCollapsed && 'Dashboard'}</a></li>
        <li><a href="/orders"><FaShoppingCart />{!isCollapsed && 'Orders'}</a></li>
        <li><a href="/products"><FaTags />{!isCollapsed && 'Products'}</a></li>
        <li><a href="/customers"><FaUsers />{!isCollapsed && 'Customers'}</a></li>
        <li><a href="/reports"><FaChartLine />{!isCollapsed && 'Reports'}</a></li>
        <li><a href="/settings"><FaCog />{!isCollapsed && 'Settings'}</a></li>
      </ul>
    </div>
  );
};

export default Sidebar;
